<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input
            placeholder=""
            class="input-with-select"
            v-model="queryInfo.query"
            clearable
            @clear="getOrdersList"
          >
            <el-button slot="append" icon="el-icon-search" @click="getOrdersList"></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="ordersList" stripe border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="order_number" label="" width="250px"></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="pay_status" label="">
          <template slot-scope="scope">
            <!-- 0 -->
            <el-tag type="danger" v-if="scope.row.pay_status === '0'"></el-tag>
            <el-tag v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="create_time" label="" width="200px">
          <template slot-scope="scope">{{scope.create_time | dateFormate}}</template>
        </el-table-column>
        <el-table-column label="">
          <template>
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="editDialogVisible=true"
            ></el-button>
            <el-button type="success" icon="el-icon-location" size="mini" @click="showLogisticsDialog"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      :before-close="handleClose"
      @close="editDialogClose"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader
            v-model="editForm.address1"
            :options="cityData"
            :props="{ expandTrigger: 'hover',label:'label',value:'value',children:'children' }"
            @change="handleChange"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogVisible = false"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="progressDialogVisible"
      width="50%"
      :before-close="handleClose"
    >
      <!--  -->
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in logisticsInfo"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata.js'
export default {
  data() {
    return {
      ordersList: [],
      //   
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      editDialogVisible: false,
      progressDialogVisible: false,
      editForm: {
        address1: [],
        address2: ''
      },
      editFormRules: {
        address1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,
      logisticsInfo: []
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    async getOrdersList() {
      const res = await this.axios.get('orders', {
        params: this.queryInfo
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ordersList = res.data.goods
      this.total = res.data.total
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrdersList()
    },
    // pagenum
    handleCurrentChange(newNum) {
      this.queryInfo.pagenum = newNum
      this.getOrdersList()
    },
    handleClose(done) {
      this.$confirm('')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    editDialogClose() {
      this.$refs.editFormRef.resetFields()
    },
    handleChange() {},
    async getLogisticsInfo() {
      const res = await this.axios.get('/kuaidi/1106975712662')
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.logisticsInfo = res.data
    },
    showLogisticsDialog() {
      this.getLogisticsInfo()
      this.progressDialogVisible = true
    }
  }
}
</script>

<style lang="less" scoped>
</style>
